.main-page-title {
  margin: 0;

  font-weight: 700;
  font-size: 25px;
  line-height: 30px;
  color: #000000;

  &--heading {
    text-align: center;

    font-size: 27px;
  }

  @media (min-width: $tablet-width) {
    font-size: 43px;
    line-height: 50px;
  }

  @media (min-width: $desktop-width) {
    font-size: 53px;
    line-height: 57px;
  }
}
